import './App.css';
import MainNavbar from './components/navBar/navbar';
import ListView from './components/listview/listview';
import SingleRestaurant from './components/singleRestaurant/singleRestaurant';
import { BrowserRouter, Routes, Route, NavLink } from "react-router-dom"; 

function App() {
  return (
    <BrowserRouter>
        <div className="App">
          <MainNavbar/>
        </div>
        
        <Routes>
            <Route exact path='/' element={<ListView/>}/>
            <Route exact path='/listview' element={<ListView/>}/>
            <Route exact path='/details/:trackingNumber/:hazardLevel' element={<SingleRestaurant />} />
        </Routes>
    </BrowserRouter>
    
  );
}

export default App;
